<template>
  <div>
    <h3>Edit Cat</h3>
    <div>
      <form @submit.prevent="updateCat" class="form-control">
        <input type="text" v-model="cat_id" required class="form-control" />
        <label>cat Id</label>
        <input type="text" v-model="cat_name" required class="form-control" />
        <label>cat Name</label>
        <br />
        <button type="submit" class="btn btn-primary">Submit</button>
        <button type="reset" @click="deleteCat" class="btn btn-danger">
          Delete
        </button>
        <router-link
          v-bind:to="{
            name: 'admin',
          }"
          class="btn btn-warning"
        >
          Cancel
        </router-link>
      </form>
    </div>
  </div>
</template>
<script>
import db from "@/firebaseConfig.js";
export default {
  name: "new-item",
  data() {
    return {
      cat_id: null,
      cat_name: null,
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("categories")
      .doc(to.params.cat_id)
      .get()
      .then((doc) => {
        next((vm) => {
          vm.cat_id = doc.data().cat_id;
          vm.cat_name = doc.data().name;
        });
      })
      .catch((err) => {
        console.log("Error getting documents", err);
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("categories")
        .doc(this.$route.params.cat_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((cat) => {
            this.cat_id = cat.data().cat_id;
            this.cat_name = cat.data().name;
          });
        });
    },
    updateCat() {
      db.collection("categories")
        .doc(this.$route.params.cat_id)
        .get()
        .then((item) => {
          item.ref
            .update({
              cat_id: this.cat_id,
              name: this.cat_name,
            })
            .then((docRef) => this.$router.push("/admin"))
            .catch((error) => console.log(err));
        });
    },
    deleteCat() {
      if (confirm("Are you sure ?")) {
        db.collection("categories")
          .doc(this.$route.params.cat_id)
          .collection("items")
          .get()
          .then((querySnapshot) => {
            querySnapshot.forEach((item) => {
              item.ref.delete().catch((error) => console.log(error));
            });
            db.collection("categories")
              .doc(this.$route.params.cat_id)
              .get()
              .then((item) => {
                item.ref
                  .delete()
                  .then((docRef) => this.$router.push("/admin"))
                  .catch((error) => console.log(err));
              });
          });
      }
    },
  },
};
</script>
